def-slider{
	min-width: 0;
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	/*  */
	.slick-slider{
		position: relative;
		padding: 0 30px;
	}
	/*   */
	.slick-slider.slick-initialized{}
	/*    */
	.slick-slider.slick-dotted{}
	/*   */
	.slick-list {
		overflow: hidden;
		flex: 1 1;
		height: 100%;
	}
	/*   */
	.slick-track{
		display: flex;
		height: 100%;
	}
	/*  */
	.slick-slide{

	}
	/*   () */
	.slick-slide.slick-active{}
	/*   */
	.slick-slide.slick-current{

	}
	/*    */
	.slick-slide.slick-center{

	}
	/*   */
	.slick-slide.slick-cloned{}
	/*  */
	.slick-arrow{
		position: absolute;
		font-size: 0;
	}

	.slick-arrow.slick-prev{
		
	}
	.slick-arrow.slick-next{
		
	}
	.slick-arrow.slick-disabled{
		border-color: #333;
	}
	/*  */
	.slick-dots{
		display: flex;
		align-items: center;
		justify-content: center;
		margin: 20px 0 0;
		padding: 0;
		flex-wrap: nowrap;
	}
	.slick-dots li{
		list-style: none;
	}
	/*   */
	.slick-dots li.slick-active{
		border-color: $flamingo-color;
	}
	/*   */
	.slick-dots li button{
		position: relative;
		padding: 0;
		margin: 0 15px;
		font-size: 0;
		background: #fff;
		border-radius: 50%;
		border-color:transparent;
		width: 16px;
		height: 16px;

	}
	.slick-dots li.slick-active button{
		background: $flamingo-color;
	}
}
.image-slider{
	filter: grayscale(1);
	@media screen and(max-width:$smTabletWidth) {
		display: none !important;
	}
}
.image-slider-big{
	margin-bottom: 30px;
	@media screen and(max-width:$smTabletWidth){
		margin-bottom: 15px;
	}
}
.image-slider,
.image-slider-big{
	width: 750px;
	@media screen and(max-width:$tabletWidth) {
		width: 535px;
	}
	@media screen and(max-width:$smTabletWidth) {
		width: 350px;
	}
	@media screen and(max-width:$phoneWidth) {
		width: calc(100vw - 100px);
	}
	@media screen and(max-width:$smPhoneWidth ) {
		width: calc(100vw - 75px);
	}
}
.image-slider__image,
.image-slider-big__image{
    width: 100%;
	border-radius: 8px;
}
.image-slider__slide{
	margin: 0 25px;
}
.image-slider__image{
	aspect-ratio: 1.8;

}
.image-slider-big__image{
	aspect-ratio: 1.9;
	@media screen and(max-width:$smTabletWidth) {
		aspect-ratio: 1.4;
	}
}

.image-slider .slick-arrow{
	position: absolute;
	font-size: 0;
	box-sizing: border-box;
	top: 35%;
	width: 25px;
	height: 25px;
	z-index: 10;
	border-radius: 3px;
	border-left:none;
	border-bottom:none;
	border-right:5px solid #fff;
	border-top:5px solid #fff;
	background-color: transparent;
	@media screen and(max-width:$tabletWidth){
		width: 15px;
		height: 15px;
		border-right:4px solid #fff;
		border-top:4px solid #fff;
	}
	@media screen and(max-width: $smTabletWidth) {
		top: 105%;
	}
}
.image-slider .slick-arrow.slick-prev{
	left: 0%;
	transform: rotate(-135deg);
}
.image-slider .slick-arrow.slick-next{
	right: 0%;
	transform: rotate(45deg);
}
.image-slider-big .slick-arrow{
	position: absolute;
	font-size: 0;
	box-sizing: border-box;
	width: 25px;
	bottom: -35px;
	height: 25px;
	z-index: 10;
	border-radius: 3px;
	border-left:none;
	border-bottom:none;
	border-right:5px solid #fff;
	border-top:5px solid #fff;
	background-color: transparent;
	@media screen and(max-width:$tabletWidth){
		width: 15px;
		height: 15px;
		border-right:4px solid #fff;
		border-top:4px solid #fff;
	}
}
.image-slider-big .slick-arrow.slick-prev{
	left: 0%;
	transform: rotate(-135deg);
}
.image-slider-big .slick-arrow.slick-next{
	right: 0%;
	transform: rotate(45deg);
}

.gallery-content{
	position: relative;
}
.slider-gallery{
	position: relative;
}
.slider-gallery-item{
	// width: calc(100% - 75%);
	// height: 320px;
}
.slider-gallery-item > img{
	filter: grayscale(1);
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
	transition: all 0.5s;
	&:hover{
		filter: grayscale(0);
	}
}
.slider-gallery .slick-arrow{
	position: absolute;
	font-size: 0;
	z-index: 10;
}
.slider-gallery .slick-arrow.slick-prev,
.slider-gallery .slick-arrow.slick-next{
	position:absolute;
	border: 3px solid #fff;
	border-radius: 50%;
	top: 46%;
	padding: 15px;
    background: transparent;
	&::before{
		content: "";
		position: absolute;
		box-sizing: border-box;
		top: 25%;
		left: 35%;
		width: 15px;
		height: 15px;
		z-index: 10;
		border-radius: 3px;
		border-left:none;
		border-bottom:none;
		border-right:4px solid #fff;
		border-top:4px solid #fff;
		background-color: transparent;
		@media screen and(max-width:$phoneWidth ) {
			width: 10px;
			height: 10px;
			border-right:3px solid #fff;
			border-top:3px solid #fff;
			border-radius: 1px;
		}
	}
	@media screen and(max-width:$smTabletWidth ) {
		top: 98%;
		bottom: 0;
	}
	@media screen and(max-width:$phoneWidth ) {
		padding: 10px;
	}
}
.slider-gallery .slick-arrow.slick-prev{
	left: 0;
	transform: rotate(-135deg);
	&::before{
		left: 20%;
	}
}
.slider-gallery .slick-arrow.slick-next{
	right: 0;
	transform: rotate(45deg);
	&::before{
		left: 20%;
	}
}
.slider-gallery .slick-dots{
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 20px 0 0;
	padding: 0;
	flex-wrap: nowrap;
}
.slider-gallery .slick-dots li{
	list-style: none;
}
/*   */
.slider-gallery .slick-dots li.slick-active{
	border-color: $flamingo-color;
}
/*   */
.slider-gallery .slick-dots li button{
	position: relative;
	padding: 0;
	margin: 0 15px;
	font-size: 0;
	background: #fff;
	border-radius: 50%;
	border-color:transparent;
	width: 16px;
	height: 16px;

}
.slider-gallery .slick-dots li.slick-active button{
	background: $flamingo-color;
}
